.member-detail-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  font-family: 'FivoSans', sans-serif;
  color: white;
}

.member-header {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
}

.member-image-large {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
}

.member-info h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: white;
}

.member-info h2 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: white;
  font-weight: normal;
}

.member-description {
  font-size: 1rem;
  color: white;
  line-height: 1.6;
}

.member-details {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.detailed-intro h3,
.projects-section h3,
.contact-section h3,
.social-media-section h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: white;
  padding-bottom: 0.5rem;
  text-align: left;
}

.detailed-intro p {
  font-size: 1rem;
  line-height: 1.8;
  color: white;
  text-align: left;
  white-space: pre-wrap;
  word-break: break-word;
}

.contact-section ul {
  list-style-type: none;
  padding-left: 0;
}

.contact-section li {
  padding: 0.5rem 0;
  font-size: 1rem;
  color: white;
  text-align: left;
}

.social-media-section ul {
  list-style-type: none;
  padding-left: 0;
}

.social-media-section li {
  padding: 0.5rem 0;
  font-size: 1rem;
  color: white;
  text-align: left;
}

.social-media-section a {
  color: white;
  text-decoration: underline;
}

.projects-section {
  padding: 1.5rem 0;
}

.projects-section ul {
  list-style-type: none;
  padding-left: 0;
}

.projects-section li {
  padding: 0.5rem 0;
  font-size: 1rem;
  color: white;
  text-align: left;
}

.projects-section li:last-child {
  border-bottom: none;
}

@media (max-width: 768px) {
  .member-header {
    flex-direction: column;
    text-align: center;
  }
  
  .member-image-large {
    width: 200px;
    height: 200px;
  }
  
  .member-info h1 {
    font-size: 2rem;
  }
}