.project {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 0px;
  margin: 1rem;
  max-width: fit-content;
  flex: 0 0 auto;
}

.project-image {
  width: 400px;
  height: 400px;
  overflow: hidden;
  border: 0px solid #e9ecef;
  border-radius: 0px;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-info {
  text-align: left;
  width: 540px;
  padding: 1rem 0;
}

.project-name {
  font-size: 1.25rem;
  font-family: "Fivo Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #FFF;
  margin: 0 0 0.5rem 0;
  line-height: 1.2;
}

.project-time {
  color: #FFF;
  line-height: 1.4;
  font-size: 0.75rem;
  font-family: "Fivo Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  margin: 0;
}

/* Responsive design */
@media (max-width: 768px) {
  .project {
    margin: 0.5rem;
    max-width: 400px;
  }
  
  .project-image {
    width: 400px;
    height: 400px;
  }
  
  .project-info {
    width: 400px;
  }
  
  .project-name {
    font-size: 1.2rem;
  }
  
  .project-time {
    font-size: 0.7rem;
  }
}

@media (max-width: 480px) {
  .project {
    margin: 0.25rem;
    max-width: 100%;
  }
  
  .project-image {
    width: 300px;
    height: 300px;
  }
  
  .project-info {
    width: 300px;
  }
  
  .project-name {
    font-size: 1.1rem;
  }
  
  .project-time {
    font-size: 0.65rem;
  }
}