/* Main container styling - full height and scrollable */
.members-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  box-sizing: border-box;
}

/* Members intro section - full height and centered */
.members-intro {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  margin: 0 auto;
}

.members-intro h1 {
  color: #ffffff;
  font-size: 6rem;
  font-weight: 700;
  line-height: 125%;
  margin-bottom: 3rem;
  margin-top: 0;
}

.members-intro p {
  font-family: 'Fivo Sans';
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.25;
  max-width: 800px;
  margin: 0;
}

/* Founding Members Section styling */
.founding-members-section {
  padding: 7.5rem 0;
  background-color: rgba(255, 255, 255, 0.1);
  width: 100%;
}

/* Section titles styling */
.section-title {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 0 0 3rem 0;
}

/* Founders container - max 3 per row */
.founders-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5rem;
  width: 100%;
}

.founders-container .founder {
  flex: 1 1 calc(25% - 1.125rem);
  max-width: 300px;
  min-width: 250px;
}

/* Members section styling - for margin and spacing control */
.members-section {
  padding: 7.5rem 0;
  width: 100%;
  margin: 0 auto;
  background-color: transparent;
}

/* Members container - max 4 per row */
.members-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5rem;
  margin: 0 auto 3rem;
  padding: 0 1rem;
  max-width: 1400px;
}

.members-container .member {
  flex: 1 1 calc(20% - 1.25rem);
  max-width: 300px;
  min-width: 240px;
}

/* Responsive layout */
@media (max-width: 1200px) {
  .founders-container .founder {
    flex: 1 1 calc(50% - 0.75rem);
    max-width: 350px;
  }
  
  .members-container .member {
    flex: 1 1 calc(33.333% - 1rem);
  }
}

@media (max-width: 900px) {
  .founders-container .founder {
    flex: 1 1 calc(50% - 0.75rem);
  }
  
  .members-container .member {
    flex: 1 1 calc(50% - 0.75rem);
  }
}

@media (max-width: 600px) {
  .members-intro h1 {
    font-size: 2rem;
  }
  
  .members-intro p {
    font-size: 1rem;
    padding: 0 0.5rem;
  }
  
  .section-title {
    font-size: 1.5rem;
    margin: 2rem 0 1rem 0;
  }
  
  .founders-container,
  .members-container {
    gap: 1rem;
    padding: 0 0.5rem;
  }
  
  .founders-container .founder,
  .members-container .member {
    flex: 1 1 100%;
    min-width: auto;
  }
}

/* Special Support section styling */
.special-support-section {
  padding: 7.5rem 0;
  background-color: rgba(255, 255, 255, 0.1);
  width: 100%;
  text-align: center;
}

.special-support-text {
  color: #ffffff;
  font-family: 'Fivo Sans';
  font-size: 1rem;
  line-height: 1.25;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
}