/* Main container styling - full height and scrollable */
.home-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  box-sizing: border-box;
}

/* Home intro section - full height and centered */
.home-intro {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  margin: 0 auto;
}

.home-intro h1 {
  color: #ffffff;
  font-size: 6rem;
  font-weight: 700;
  line-height: 125%;
  margin-bottom: 3rem;
  margin-top: 0;
}

.home-intro p {
  font-family: 'Fivo Sans';
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.25;
  max-width: 800px;
  margin: 0;
}

/* Missions section styling */
.missions {
  padding: 7.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 0 auto;
}

.missions p {
  font-family: 'Fivo Sans';
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.25;
  max-width: 800px;
  margin: 0;
  text-align: center;
}

.missions p + p {
  margin-top: 2.5rem;
}

/* Home Projects Section */
.home-projects-section {
  padding: 7.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
}

.home-projects-section h2 {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 3rem;
  text-align: center;
}

.home-projects-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  max-width: 800px;
  width: 100%;
  margin: 0 auto 3rem auto;
}

/* Home Members Section */
.home-members-section {
  padding: 7.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 0 auto;
}

.home-members-section h2 {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 3rem;
  text-align: center;
}

.home-members-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  max-width: 1000px;
  width: 100%;
  margin-bottom: 3rem;
}

/* View All Button Styling */
.view-all-btn {
  background-color: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 400;
  text-decoration: none;
  border-radius: 0px;
  transition: all 0.3s ease;
  cursor: pointer;
  text-align: center;
}

.view-all-btn:hover {
  background-color: #ffffff;
  color: #000000;
  transform: translateY(-2px);
}

/* Tablet responsive layout */
@media (max-width: 900px) and (min-width: 601px) {
  .home-projects-container {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .home-members-container {
    flex-wrap: wrap;
    justify-content: center;
  }
}

/* Responsive layout */
@media (max-width: 600px) {
  .home-intro h1 {
    font-size: 2rem;
  }
  
  .home-intro p {
    font-size: 1rem;
    padding: 0 0.5rem;
  }
  
  .missions {
    padding: 7.5rem 0.5rem;
  }
  
  .missions p {
    font-size: 1rem;
    padding: 0 0.5rem;
  }
  
  .home-projects-section, .home-members-section {
    padding: 4rem 0.5rem;
  }
  
  .home-projects-section h2, .home-members-section h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  
  .home-projects-container {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .home-members-container {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .view-all-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}