.footer {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 3rem 0 2rem 0;
  margin-top: auto;
  width: 100%;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.footer-title {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  font-family: "Fivo Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  text-align: center;
  margin: 0;
  margin-bottom: 1rem;
}

.footer-links {
  display: flex;
  gap: 3rem;
  align-items: center;
}

.footer-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #ffffff;
  text-decoration: none;
  font-family: "Fivo Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  transition: color 0.3s ease, transform 0.3s ease;
  cursor: pointer;
}

.footer-link:hover {
  color: #cccccc;
  transform: translateY(-2px);
}

.footer-icon {
  font-size: 1.2rem;
}

.footer-copyright {
  text-align: center;
}

.footer-copyright p {
  color: #888888;
  font-family: "Fivo Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  font-size: 0.875rem;
  margin: 0;
  line-height: 1.4;
}

/* Responsive design */
@media (max-width: 768px) {
  .footer {
    padding: 2rem 0 1.5rem 0;
  }
  
  .footer-content {
    padding: 0 1rem;
    gap: 1.5rem;
  }
  
  .footer-title {
    font-size: 1.5rem;
  }
  
  .footer-links {
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .footer-link {
    font-size: 0.9rem;
  }
  
  .footer-copyright p {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .footer-links {
    gap: 1.5rem;
    flex-direction: column;
  }
  
  .footer-content {
    gap: 1rem;
  }
}