.founder-link {
  text-decoration: none;
  color: inherit;
  display: block;
}

.founder {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 8px;
  padding: 1.5rem;
  margin: 1rem;
  transition: transform 0.3s ease;
  /* Remove fixed width to allow flexbox control */
  max-width: fit-content;
  flex: 0 0 auto;
}

.founder:hover {
  transform: translateY(0px);
}

.founder-image {
  width: 200px;
  height: 250px;
  overflow: hidden;
  border: 0px solid #e9ecef;
}

.founder-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(100%);
  transition: filter 0.3s ease, transform 0.3s ease;
}

.founder-image:hover img {
  filter: grayscale(0%);
  transform: scale(1.05);
}

.founder-info {
  text-align: center;
  width: 200px;
}

.founder-name {
  font-size: 1.25rem;
  font-family: "Fivo Sans";
  font-style: normal;
  font-weight: 400;
  color: #ffffff;
  margin: 1.5rem 0 1.5rem 0;
}

.founder-label {
  color: #ffffff;
  font-weight: 400;
  font-size: 1rem;
  font-family: "Fivo Sans";
  font-style: normal;
  margin: 0 0 0.75rem 0;
  letter-spacing: 0.5px;
}

.founder-description {
  color: #ffffff;
  line-height: 1.5;
  font-size: 0.75rem;
  font-family: "Fivo Sans";
  font-style: normal;
  margin: 0;
}

/* Responsive design */
@media (max-width: 768px) {
  .founder {
    margin: 0.5rem;
    padding: 1rem;
    max-width: 280px;
  }
  
  .founder-image {
    width: 100px;
    height: 100px;
  }
  
  .founder-name {
    font-size: 1.1rem;
  }
  
  .founder-description {
    font-size: 0.9rem;
  }
}