/* Main container styling - full height and scrollable */
.projects-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  box-sizing: border-box;
}

/* Projects intro section - full height and centered */
.projects-intro {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  margin: 0 auto;
}

.projects-intro h1 {
  color: #ffffff;
  font-size: 6rem;
  font-weight: 700;
  line-height: 125%;
  margin-bottom: 3rem;
  margin-top: 0;
}

.projects-intro p {
  font-family: 'Fivo Sans';
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.25;
  max-width: 800px;
  margin: 0;
}

/* Projects section styling */
.projects-section {
  padding: 7.5rem 0;
  width: 100%;
  margin: 0 auto;
  background-color: transparent;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
  padding: 0 1rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.projects-grid .project {
  flex: 0 0 calc(33.333% - 1.33rem);
  max-width: calc(33.333% - 1.33rem);
}

/* Center the grid on smaller screens */
@media (max-width: 768px) {
  .projects-intro h1 {
    font-size: 4rem;
  }
  
  .projects-intro p {
    font-size: 1rem;
    padding: 0 0.5rem;
  }
  
  .projects-grid {
    justify-content: center;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
  
  .projects-grid .project {
    flex: 0 0 auto;
    max-width: none;
  }
}

@media (max-width: 600px) {
  .projects-intro h1 {
    font-size: 3rem;
  }
}